* {
    direction: rtl;
    text-decoration: none;
    margin: 0px;
    padding: 0px;
    text-align: right;
}

.br-20 {
    border-radius: 20px !important;
}

.mt-200 {
    margin-top: 200px !important;
}

.pt-150 {
    padding-top: 150px !important;
}

.pt-68 {
    padding-top: 68px;
}

.h-250 {
    height: 250px;
}

.h-22-vw {
    height: 22vw;
}

.h-70-vw {
    height: 70vw;
}

.carousel-indicators {
    direction: ltr !important;
}

.fs-large {
    font-size: 1.5rem;
}

.flex-nowrap {
    flex-wrap: nowrap;
}

.of-scroll {
    overflow: scroll;
}

.sb-hidden {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.sb-hidden::-webkit-scrollbar {
    display: none;
}

.carousel-input-wrapper {
    background-color: #afddfc;
    color: #1d6cd4;
    border-radius: 1.5rem;
    font-weight: bold;
    text-align: center;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.carousel-wrapper {
    background-color: #afddfc;
    color: #1d6cd4;
    border-radius: 1.5rem;
    font-weight: bold;
    text-align: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.carousel-input {
    height: calc(1em + 0.75rem + 2px);
    padding: 0;
    font-size: 0.8rem;
    background-color: #afddfc;
    text-align: center;
}

.custom-input {
    background: none;
}

.custom-input-border {
    border: none;
    border-bottom: 1px solid #aaa;
    border-radius: 0px;
}

.nav-item-2 {
    img {
        width: 10px !important;
        display: none !important;
    }
}

.z-999 {
    z-index: 999 !important;
}

.top-10-px {
    top: 10px !important;
}

.w-200-px{
    width: 200px !important;
}

.add-light-gray-bg-on-hover{
    background-color: inherit;
    transition: 500ms;
    border-radius: 5px;
    &:hover{
        background-color: lightgrey !important;
    }
}