.sp-add-user-input {
    border: none;
    border-bottom: 1px solid #777;
    background: none;
    width: 90%;
    text-align: center;
}

.sp-user-item {
    color: #fff;
    cursor: pointer;
    font-weight: bold;
    padding: 15px 25px;
    padding-left: 10px;
    padding-bottom: 5px;
    border-radius: 15px;
    background-color: #2196f3;
    box-shadow: 0 9px #0d89ed;
    background-repeat: no-repeat;
    background-position: top right;
}

.sp-user-item-x {
    background-image: url("../../assets/icons/user.png");
    background-size: 50px 50px;
}
